@import '../../../../Default.scss';

.List{
    width: 100%;
    overflow-y: auto;
}

.Active{
    color: #1565C0;
}

.Inactive{
    color: $black;
}

.Center{
    div{
        text-align: center;
    }
}

.GreyBtn{
    span{
        color: $grey;
    }
}

.RedBtn{
    span{
        color: $red;
    }
}

@media only screen and (min-width: 375px) {
    .List{
        width: 90%;
    }
}
@media only screen and (min-width: 525px) {
    .List{
        width: 425px;
    }
}