/* -------------------- CSS Reset -------------------- */
@import-normalize;

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1,h2,h3,h4,h5,h6{
  margin: 0;
  padding: 0;
}

button{
  cursor: pointer;
  background: transparent;
  border: none;
}
  
button:hover, button:focus{
  outline: none;
}

html{
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
}

body{
    background: #f5f5f5;
    font-family: inherit;

    padding-right: 0 !important;
}