@import '../../../../Default.scss';

.Paper{
    width: 90%;
    
    @extend %flex-center-spaceBetween;
    
    position: absolute;

    bottom: $extraSmall;

    button{
        svg{
            font-size: $small;
        }
    }
}  

.BlackBtn{
    span{
        color: $black;
    }
}

.PrimaryBtn{
    span{
        color: $primary;
    }
}


@media only screen and (min-width: 525px) {
    .Paper{
        width: 50%;
        
        bottom: $small;
    }  
}


@media only screen and (min-width: 1280px) {
    .Paper{
        width: 640px;
    }  
}