@import '../../../../Default.scss';

.Time{
    width: 100%;
    @extend %flex-center-spaceBetween;
    justify-content: space-between;
}

.TimeGroup{
    @extend %flex-center-spaceBetween;
    flex-direction: column;
    
}

.Tooltip{
    width: 100%;
}


.Hidden{
    visibility: hidden;
}

.GreyBtn{
    span{
        color: $grey;
    }
}

@media only screen and (min-width: 375px) {
    .Time{
        width: 90%;
    }

    .TimeGroup{
        h2{
            font-size: 80px;
        }
    }
}

@media only screen and (min-width: 425px) {
    .TimeGroup{
        h2{
            font-size: 96px;
        }
    }
}

@media only screen and (min-width: 525px) {
    .Time{
        width: 370px;
    }
}