@import '../../../../Default.scss';

.ButtonGroup{
    width: 100%;
    
    margin: 1.5rem 0;

    @extend %flex-center-spaceBetween;
    justify-content: center;
    
    span{
        color: $black;
    }

    button{
        width: 100%;
    }

    .Right{
        margin-left: 0.5rem;
    }
    
    .Left{
        margin-right: 0.5rem;
    }
}

.GreyBtn{
    span{
        color: $grey;
    }
}

.RedBtn{
    span{
        color: $red;
    }
}

.GreenBtn{
    span{
        color: $green;
    }
}


@media only screen and (min-width: 375px) {
    .ButtonGroup{
        width: 90%;
    }
}

@media only screen and (min-width: 525px) {
    .ButtonGroup{
        width: 370px;
    }
}