@import '../../../../../../Default.scss';

.Time{
    width: 100%;
    @extend %flex-center-spaceBetween;
    justify-content: center;
}

.TimeGroup{
    @extend %flex-center-spaceBetween;
    flex-direction: column;
    margin: 0 1rem;
}

.Tooltip{
    width: 100%;
}

.Hidden{
    visibility: hidden;
}

.GreyBtn{
    span{
        color: $grey;
    }
}

@media only screen and (min-width: 375px) {
    .TimeGroup{
        h3{
            font-size: 72px;
        }
    }
}

@media only screen and (min-width: 425px) {
    .TimeGroup{
        h3{
            font-size: 96px;
        }
    }
}