@import '../../Default.scss';

.Container{
    height: calc(100vh - 80px);
    @extend %flex-center-spaceBetween;
    flex-direction: column;
    justify-content: flex-start;
    
    margin-top: 1rem;
}

.Time{
    @extend %flex-center-spaceBetween;
    justify-content: center;
}

.ButtonGroup{
    width: 100%;
    
    margin: 1rem 0;

    @extend %flex-center-spaceBetween;
    justify-content: center;
    
    span{
        color: $black;
    }

    button{
        width: 100%;
        margin: 0 0.5rem;
    }
}

.GreyBtn{
    span{
        color: $grey;
    }
}

.RedBtn{
    span{
        color: $red;
    }
}

.GreenBtn{
    span{
        color: $green;
    }
}

.List{
    width: 100%;
    overflow-y: auto;
}

.Fastest{
    color: $green;
}

.Slowest{
    color: $red;
}

.Normal{
    color: $black;
}

@media only screen and (min-width: 375px) {
    .ButtonGroup{
        width: 90%;
    }

    .List{
        width: 90%;
    }
}
@media only screen and (min-width: 525px) {
    .ButtonGroup{
        width: 370px;
    }

    .List{
        width: 370px;
    }
}