@import '../../../../Default.scss';

.Content{
    @extend %flex-center-spaceBetween;
    flex-direction: column;
    justify-content: center;
    
    .Title{
        span{
            margin-left: 0.25rem;
            font-size: 1rem;
        }
    }
    
    .Subtitle{
        font-size: $extraSmall;
    }
}

@media only screen and (max-width: 320px) {
    .Content{
        .Title{
            font-size: 5rem;
            span{
                font-size: 0.75rem;
            }
        }
        
        .Subtitle{
            font-size: 0.875rem;
        }
    }
}